@import url(https://fonts.googleapis.com/css?family=Abril+Fatface|Roboto+Mono:400,700&display=swap);
body{background-color:#fff}

header{width:100%;height:auto;background-color:#000;display:flex;flex-direction:column;align-items:center;justify-content:space-evenly;color:#fff}h1{font-size:3rem;margin:1rem 0;font-family:'Abril Fatface', serif;letter-spacing:0.2rem;text-align:center;font-weight:400;cursor:pointer}.icon-menu{font-size:2rem;cursor:pointer;margin:0.5rem 0;z-index:90}.icon-menu:hover{color:silver}nav{width:100%;height:calc(100vh - 145px);display:none}ul{width:100%;height:calc(100% - 145px);display:flex;flex-direction:column;background-color:#000;position:absolute;top:145px}ul li{width:100%;height:100%;font-size:1.5rem;text-align:center;color:#fff;cursor:pointer;display:flex;align-items:center;justify-content:space-around}ul li:hover{background-color:#fff;color:#000;font-weight:600}@media screen and (min-width: 670px){h1{font-size:6rem}.icon-menu{display:none}nav{display:block !important;z-index:0 !important;height:auto}ul{width:100%;display:flex;flex-direction:row;align-items:center;justify-content:space-around;background-color:transparent;position:relative;top:0}ul li{width:100%;height:100%;font-size:1.5rem;text-align:center;padding:0.5rem}ul li:hover{background-color:#fff;color:#000}}@media screen and (max-width: 400px){.icon-menu{margin:0.5rem;align-self:flex-end}}

.container{width:100%;height:auto;display:grid;grid-template-columns:repeat(auto-fit, minmax(300px, 1fr));grid-gap:1rem;padding:1.5rem;justify-content:center}.box{border:5px solid #000;background-color:#fff;position:relative;padding-bottom:2rem}@media screen and (min-width: 1000px){.box:first-child{grid-row:1/3;grid-column:1/3}.box:first-child h3{font-size:2.5rem}.box:first-child .image{height:400px}.box:first-child p{font-size:1.5rem}}

.title{font-size:1.4rem;text-transform:uppercase;text-align:center}.image{width:100%;height:200px;background-size:cover;background-repeat:no-repeat;background-position:center;margin:0.5rem 0}.title,.description,.link{padding:1rem}.link{position:absolute;bottom:0;right:0}.link a{color:midnightblue;font-weight:600}

.search-bar{position:absolute;top:10px;right:10px;font-size:1.5rem;display:flex;align-items:center}.search-bar .search-icon{color:#fff;margin-left:0.5rem}.search-bar input{padding:0.5rem;border-radius:10px;border:none;width:12rem}@media screen and (max-width: 670px){.search-bar{top:105px;left:10px}.search-bar input{width:8rem}}

*{margin:0;padding:0;box-sizing:border-box;scroll-behavior:smooth}li{list-style:none}a{text-decoration:none}body{width:100%;height:auto;font-family:'Roboto Mono', serif}

